.container {
    display: flex;
    align-items: center;
    width: 100%;
    justify-content: center;
  }
  .deck {
    position: absolute;
    width: 100vw;
    height: 50vh;
    will-change: transform;
    display: flex;
    align-items: center;
    justify-content: center;
    touch-action: none;
  }
  
  .deck > div {
    background-color: rgba(255, 255, 255, 0);
    width: 30vw;
    max-width: 30vw;
    height: 30vh;
    max-height: 30vh;
    will-change: transform;
    border: 3px solid white;
    border-radius: 10px;
    box-shadow: 0 12.5px 50px -10px rgba(50, 50, 73, 0.4), 0 10px 10px -10px rgba(50, 50, 73, 0.3);
    justify-content: center;
    align-items: center;
} 
  
  .Text{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 2rem;
    color: blue;
  }

