
.container{
    display: flex;
    flex-flow: column nowrap;
    height: auto;
    width: 100vw;
    color: white;
    padding-top: 5vh;
    justify-content: space-around;
    align-items: center;
  }

.strongDesign{
  color:rgb(4, 243, 131);
  line-height: 235%;
  font-size: 9vmin;
  font-family: '워헤븐';
}
.SubStrong{
  color:rgb(207, 243, 4);
  font-size: 6vmin;
  font-family: '워헤븐';
  line-height: 200%;
}

.firstSection{
    display: flex;
    width: 100vw;
    flex-flow: column nowrap;
    align-items: center;
    background-image:linear-gradient( rgba(0, 0, 0, 0.9),rgba(0, 0, 0, 0.7) ) ,
    url('../img/defaultimg2.png');
    background-size: cover;
    background-position: center;
  }


.Introduce{
  flex-flow: row  wrap;
  text-align: center;
  animation: fadeInFromRight 1s ease-in-out forwards; /* 이미지의 페이드인 애니메이션 적용 */
  margin-bottom: 3vh;
}

.SubTitle{
  font-family: '워헤븐';
  font-size: 4vmin;
}

.Title{
  font-family: '워헤븐';
  font-size: 6vmin;
}


.FirstImage{
  width: 50%;
  height: auto;
  border-radius: 20%;
  border: 3px solid white;
  transform: rotate(-10deg);
}


.AboutClub{
  font-family: '스윗';
  font-size: 4vmin;
  text-align: center;
  margin-bottom: 5vh;
  animation: fadeInFromBottom 1s ease-in-out forwards; /* 이미지의 페이드인 애니메이션 적용 */
}

.secondSection{
  display: flex;
  width: 100vw;
  height: 100vh;
  flex-flow: column nowrap;
  background-image:
  linear-gradient( rgba(0, 0, 0, 0.2),
  rgba(0, 0, 0, 0.7) ) ,
  url('../img/intro.png');
  background-size: cover;
  background-position: center;
  padding: 5vh 0vw 0vh 3vw;
  align-items: center;
  justify-items: center;
}

.SloganSection{
  flex-flow: row  wrap;
  height: auto;
  width: 80%;
  font-family: '스윗';
  font-size: 4vmin;
  text-align: center;
  animation: fadeInFromBottom 1s ease-in-out forwards; /* 이미지의 페이드인 애니메이션 적용 */
  border-radius: 16px;
  background: rgba(255, 255, 255, 0.15);
  box-shadow: 0px 0px 4px 0px #979797;
  backdrop-filter: blur(9.999999046325684px);
  box-shadow: 0px 0px 4px 0px #979797;
  backdrop-filter: blur(10px);
  margin: 3vh 3vw 3vh 3vw;
}
.StrongSection {
  display: flex; /* Flexbox 사용 */
  flex-flow: row wrap;
  width: 100vw;
  font-family: '워헤븐';
  justify-content: space-around;
  animation: fadeInFromRight 1s ease-in-out forwards; /* 이미지의 페이드인 애니메이션 적용 */
  row-gap: 5vh;
}

.StrongSlogan{
  font-style: italic;
  color: rgb(4, 243, 131);
  font-size: 9vmin;
  font-family: '워헤븐';
}
.SubSlogan{
  color:rgb(207, 243, 4);
  font-size: 5vmin;
  font-family: '워헤븐';
  line-height: 300%;
}

.StrongSection .text {
  flex: 2; /* 텍스트 영역이 자동으로 남은 공간을 채우도록 함 */
}

.SecondImage {
  flex: 1; /* 이미지 영역이 자동으로 남은 공간을 채우도록 함 */
  max-width: 390px;
  height: auto;
  border-radius: 20%;
  border: 3px solid white;
  transform: rotate(10deg);
}


/* CSS 코드 */
@keyframes fadeInFromLeft {
  from {
    opacity: 0;
    transform: translateX(-50px); /* 시작 위치 */
  }
  to {
    opacity: 1;
    transform: translateX(0); /* 최종 위치 */
  }
}

@keyframes fadeInFromRight {
  from {
    opacity: 0;
    transform: translateX(50px); /* 시작 위치 */
  }
  to {
    opacity: 1;
    transform: translateX(0); /* 최종 위치 */
  }
}

@keyframes fadeInFromBottom {
  from {
    opacity: 0;
    transform: translateY(50px); /* 시작 위치 */
  }
  to {
    opacity: 1;
    transform: translateY(0); /* 최종 위치 */
  }
}

@keyframes fadeInFromTop {
  from {
    opacity: 0;
    transform: translateY(-50px); /* 시작 위치 */
  }
  to {
    opacity: 1;
    transform: translateY(0); /* 최종 위치 */
  }
}
