/* Chat Container */
.chatContainer {
    position: fixed;
    bottom: 2vh;
    right: 2vh;
    z-index: 1000;
    transition: all 0.5s ease;
    background-color: #000;
    border-radius: 20px;
    padding: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  }
  
  .chatContainer.open {
    bottom: 45vh;
    right: 45vh;
  }
  
  /* Chat Button */
  .chatButton {
    position: absolute;
    bottom: 0;
    right: 0;
    width: 80px;
    height: 80px;
    background-color: #00ff556e;
    color: #000000;
    font-family: '워헤븐'; /* 버튼 텍스트는 하얀색으로 */
    border: none;
    border-radius: 50%;
    cursor: pointer;
    transition: all 0.5s ease;
  }
  
  .chatButton:hover {
    background-color: #0056b3;
  }
  
  /* Modal */
  .modal {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: rgba(0, 0, 0, 0.8);
    border-radius: 20px;
    padding: 20px;
    transition: all 0.5s ease;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  
  .modalContent {
    width: 80vw;
    height: 80vh;
    overflow-y: auto;
  }
  
  /* Header */
  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
    /* Header */
    position: sticky;
    z-index: 1;
    color: white;
    font-family: '스윗';
  }
  

  
  .closeButton {
    background-color: transparent;
    border: none;
    color: #fff;
    font-size: 16px;
    cursor: pointer;
    transition: all 0.3s ease;
  }
  
  .closeButton:hover {
    color: #f44336;
  }
  
  /* Input Container */
  .inputContainer {
    display: flex;
    align-items: center;
    position: absolute;
    bottom: 10vh;
    width: calc(100% - 40px);
  }
  
  .inputField {
    flex: 1;
    padding: 10px;
    margin-right: 10px;
    border-radius: 5px;
    border: 1px solid #ccc;
    color: #000; /* 인풋 박스 텍스트는 검정색으로 */
  }
  
  .sendButton {
    padding: 10px 20px;
    background-color: #00f008;
    color: #052001;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: all 0.3s ease;
  }
  
  .sendButton:hover {
    background-color: #45a049;
  }
  /* Message Text */
/* Chat Body */
.body {
    flex: 1;
    overflow-y: auto;
    padding: 10px;
  }
  
  /* Message Container */
  .messageContainer {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
    font-family: '스윗';
  }
  
  /* Profile Picture */
  .profilePicture {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    margin-right: 10px;
  }
  
  /* Message */
  .message {
    background-color: #ffffff; /* 채팅 메시지의 배경색 */
    border-radius: 10px;
    padding: 10px;
  }
  
  /* Username */
  .message strong {
    color: #0e5505; /* 사용자 이름의 색상 */
    margin-right: 5px;
  }
  